import React, { useEffect } from 'react'
import PageHeader from '../../components/pageHeader/pageHeader'
import PageContent from '../../components/pageContent/pagecontent'
import { Box, Text } from 'fusion'
import Content from '../../components/content'
import { Link } from 'theme-ui'
import { SEO } from '../../components/seo'
import HeaderAnchor from '../../components/headerAnchor'

const boldText = {
    fontFamily: "Basier Square SemiBold,Helvetica,Arial,sans-serif"
}

const slug = "Icons"

export default function icons() {
    return (
        <>
            <SEO title={slug} />
            <PageContent>
                <PageHeader
                    heading={slug}
                    description="Icons are used to visually represent common actions, concepts, entities or features. They are simple, informative and are a key part of our visual language."
                />
                <Content>
                    <Box className="textControl">
                        <HeaderAnchor anchor="Principles">
                            <Text variant="heading2" className="h2">Principles</Text>
                        </HeaderAnchor>
                        <HeaderAnchor anchor="Simplicity">
                            <Text variant="heading3">Simplicity</Text>
                        </HeaderAnchor>
                        <Text mb={4} as="p">Highly detailed icons increase cognitive load. Focus on simplicity to help our users understand the concept the icon represents and ensure readability is maintained on smaller screens.</Text>

                        <HeaderAnchor anchor="Naming conventions">
                            <Text variant="heading3">Naming conventions</Text>
                        </HeaderAnchor>
                        <Text mb={4} as="p">In general, we name our icons literally, not metaphorically. For example, an 'x' symbol may often be used to close or remove something from the UI. As icons could be used for multiple use cases, we keep our names generic. An 'x' therefore is named "x" in our system, not "close". A '+' symbol is named "plus" and not "add".</Text>

                        <HeaderAnchor anchor="Form">
                            <Text variant="heading3">Form</Text>
                        </HeaderAnchor>
                        <Text mb={5} as="p">The design of an icon communicates tone, much like the content it’s paired with. Focus on the effectiveness of the message instead of its ability to delight. In other words, prioritize representing the function, rather than how nice it looks.</Text>

                        <HeaderAnchor anchor="Creating icons">
                            <Text variant="heading2" className="h2">Creating icons</Text>
                        </HeaderAnchor>
                        <HeaderAnchor anchor="Use established icons">
                            <Text variant="heading3">Use established icons</Text>
                        </HeaderAnchor>
                        <Text mb={4} as="p">Icons that have been used globally and for a long time have a better chance of being recognized and understood quickly. We don't need to reinvent an icon that’s already been accepted as a common convention. Conventions already exist for concepts like "edit", “delete”, “settings”, “search”. These symbols are widely recognized and don’t need to be redefined in our system.</Text>

                        <HeaderAnchor anchor="Feather as a base">
                            <Text variant="heading3">Feather as a base</Text>
                        </HeaderAnchor>
                        <Text mb={4} as="p">Our system icon library used <Link href="https://feathericons.com">feather-icons</Link> as a base. This is a good place to start if you need to add a new icon to the system. What you need may already exist there. If not, use this library as a guide to inform your decisions when creating a new icon.</Text>

                        <HeaderAnchor anchor="Limit variations">
                            <Text variant="heading3">Limit variations</Text>
                        </HeaderAnchor>
                        <Text mb={4} as="p">Use a single icon to represent variations of a concept. User settings, admin settings, account settings, and any new types of settings should all use the same icon. Don’t create a custom icon for each of these concepts as it will bloat the system and make it harder to use.</Text>

                        <HeaderAnchor anchor="Keep internationalization in mind">
                            <Text variant="heading3">Keep internationalization in mind</Text>
                        </HeaderAnchor>
                        <Text mb={5} as="p">Where possible, use forms that will be recognized internationally. When deciding what shape should be used, try to ensure that it will be understood, at a glance, by users with different cultural backgrounds.</Text>

                        <HeaderAnchor anchor="Using icons">
                            <Text variant="heading2" className="h2">Using icons</Text>
                        </HeaderAnchor>

                        <HeaderAnchor anchor="Pair icons with text">
                            <Text variant="heading3">Pair icons with text</Text>
                        </HeaderAnchor>
                        <Text mb={2} as="p">An icon's purpose is to clarify content by providing a visual cue and improve legibility and scannability.
                            In most cases, icons should be used alongside a label. Never use an icon to replace a name of a product or feature.
                            The only exception to this is an icon that is a common convention, like an 'x' icon that closes a modal.</Text>
                        <Text mb={4} as="p">As part of this pattern, it is also good practice to keep the icon color consistent with the text that it is connected to.</Text>

                        <HeaderAnchor anchor="When to use icons">
                            <Text variant="heading3">When to use icons</Text>
                        </HeaderAnchor>
                        <Text mb={4} as="p">Icons should be used with care. Overusing them can clutter the interface and become distracting. Icons are commonly used for:</Text>
                        <Text mb={5} as="p">
                            <ul>
                                <li>Inline with a heading or title to enhance clarity</li>
                                <li>In banners to draw attention to something important, like an error</li>
                                <li>To direct attention towards something that a user can take action on</li>
                            </ul>
                        </Text>

                        <HeaderAnchor anchor="Icon types">
                            <Text variant="heading2" className="h2" >Icon types</Text>
                        </HeaderAnchor>
                        <Text as="p" mb={4}>
                            Fusion contains two icon libraries that we can leverage in our applications:
                            <ul>
                                <li><span style={boldText}>System icons</span> are used to represent an action (upload, filter) or a data point (date, user-type) in the UI. System icons are designed to be clear, simple modern. Each icon is reduced to a minimal form, expressing essential characteristics. </li>
                                <li><span style={boldText}>Rich icons</span> are used when we need to provide some extra visual flair or draw the user's attention to a certain area of the screen.  </li>
                            </ul>
                        </Text>
                        <Link href="https://www.figma.com/file/SLn5GA1m3uhHFu0bnGvffh/1.-FDS---Foundations?node-id=3%3A6">→ View our icon libraries in Figma</Link>
                    </Box>
                </Content>
            </PageContent>
        </>
    )
}
